.navbar{
    position:fixed;
    display: flex;
    justify-content: space-around;
    margin-top: 0px;
    padding: 20px;
    background-color:azure;
    top: 0px;
    left: 0px;
    z-index: 0;
    width: 100%;
    
}
.linkstyle{
   text-decoration: none;
   cursor: pointer;
   font-size: 30px;
   font-weight: 300;
   font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.linkstyle:hover{
  color: #fdc50f;
}
 #improve{
  font-family:var(--purple);
  font-size: 30px;
  font-weight: 400;
 }
 @media screen and (max-width: 480px) {
    .navbar {
      display: grid;
       
    }
    .linkstyle{
        font-size: 10px;
        justify-content: end;
    }
  }

 